.specialmenu {
  flex-direction: column;
  background: var(--color-black);
}

.specialmenu-title {
  margin-bottom: 2rem;
  text-align: center;
}

.specialmenu-menu {
  width: 100%;
  margin: 2rem 0;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
}

.specialmenu-menu-heading {
  font-family: var(--font-base);

  font-weight: 600;
  font-size: 4.5rem;
  line-height: 5.85rem;
  letter-spacing: 0.2rem;
  color: var(--color-white);
}

.specialmenu-menu-dishes,
.specialmenu-menu-juices {
  flex: 1;
  width: 100%;
  flex-direction: column;
}

.specialmenu-menu-img {
  width: 42rem;
  margin: 0 2rem;
}
.specialmenu-menu-img img {
  width: 100%;
  height: auto;
}

.specialmenu-menu-lists {
  display: flex;
  flex-direction: column;
  margin: 2rem 0rem;
  width: 100%;
}

@media screen and (max-width: 2000px) {
  .specialmenu-menu-img {
    width: 65rem;
  }

  .specialmenu-menu-img img {
    height: 92rem;
  }
}
@media screen and (max-width: 1150px) {
  .specialmenu-menu {
    width: 100%;
    align-items: center;
    flex-direction: column;
  }
  .specialmenu-menu-img {
    display: none;
  }

  .specialmenu-menu-juices {
    margin-bottom: 5rem;
  }
}
@media screen and (max-width: 650px) {
  .specialmenu-menu-img {
    width: 100%;
  }

  .specialmenu-menu-heading {
    font-size: 3.5rem;
    line-height: 4.85rem;
  }
}
