.specialmenuitem {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 3rem 0;
}
.specialmenuitem-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0;
}

.specialmenuitem-name {
  flex: 1;
}

.specialmenuitem-name h2 {
  color: var(--color-golden);
}

.specialmenuitem-desc {
  margin: 0;
}

.specialmenuitem-tags {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-align: left;
  width: 100%;
  margin: 1rem 0 0 0;
}

.specialmenuitem-tags p,
.specialmenuitem-tags button {
  margin: 0;
}
.seperator {
  background: var(--color-golden);
  width: 9rem;
  height: 0.1rem;
  margin: 0 1rem;
}

.specialmenuitem-price {
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
}

.specialmenuitem-actions {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin: 1rem 0 0 0;
  gap: 2rem;
}

.specialmenuitem-actions button {
  font-family: var(--font-alt);
  font-weight: 400;
}
.specialmenuitem-actions button:hover {
  background-color: var(--color-golden);
  color: var(--color-white);
}

.specialmenuitem-actions a:link,
.specialmenuitem-actions a:visited {
  color: var(--color-white);
}
.specialmenuitem-actions a:hover,
.specialmenuitem-actions a:active {
  color: var(--color-golden);
}

@media screen and (min-width: 2000px) {
  .specialmenuitem-actions button {
    font-size: 3rem;
    line-height: 4rem;
  }
  .specialmenuitem-actions a {
    font-size: 2.5rem;
  }
}
