.chef-content {
  display: flex;
  flex-direction: column;
  width: 100%;

  margin-top: 5rem;
}

.chef-content-quote {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
}
.chef-content-quote img {
  width: 4.7rem;
  height: 4rem;
  margin: 0 1rem 1rem 0;
}
.chef-sign {
  width: 100%;
  margin-top: 3rem;
}
.chef-sign p:first-child {
  font-family: var(--font-base);
  font-weight: 400;
  font-size: 3.2rem;
  line-height: 4.1rem;
  letter-spacing: 0.2rem;
  text-transform: capitalize;
  color: var(--color-golden);
}

.chef-sign img {
  width: 25rem;
  margin-top: 3rem;
}

@media screen and (min-width: 2000px) {
  .h1 {
    font-size: 13rem;
  }
  .chef-sign img {
    width: 50rem;
  }
  .chef-content-quote img {
    width: 8.7rem;
    height: 8rem;
    margin: 0 2rem 1rem 0;
  }
  .chef-sign p:first-child {
    font-size: 4.4rem;
  }
}
@media screen and (max-width: 2000px) {
  .chef-sign img {
    width: 35rem;
  }
}

@media screen and (max-width: 450px) {
  .chef-sign img {
    width: 60%;
  }
  .chef-content-quote img {
    width: 6.7rem;
    height: 6rem;
    margin: 0 1rem 1rem 0;
  }
}
@media screen and (max-width: 350px) {
  .h1 {
    font-size: 3.5rem;
  }
}
